"use client";

import React, { useState, useEffect, ReactNode, useCallback } from "react";
import Link from "next/link";
import { usePathname, useRouter } from "next/navigation";
import {
  YadakchiAdminLayout,
  InfoCard,
  Label,
  ActionToolbar,
  EntityHeader,
  Badge,
  Button, // ุงููพูุฑุช Button
  FormModal, // ุงููพูุฑุช FormModal
  Input,     // ุงููพูุฑุช Input
} from "@my-ds/design-system";

import { getWarrantyById, updateWarranty } from "../../services/api/warrantyService";
import { Warranty } from "@/services/api/types";
interface WarrantyDetailsPageProps {
  params: { id: string; }
}

export default function WarrantyDetailsPage({ params }: WarrantyDetailsPageProps) {
  const { id } = params;
  const router = useRouter();
  
  const [warranty, setWarranty] = useState<Warranty | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  
  // State ูุง ูุฑุจูุท ุจู ููุฏุงู ูุฑุงุด
  const [isEditModalOpen, setIsEditModalOpen] = useState(false);
  const [formTitle, setFormTitle] = useState('');
  const [actionLoading, setActionLoading] = useState(false);
  
  const pathname = usePathname();
  const renderLink = (href: string, children: ReactNode) => <Link href={href}>{children}</Link>;
  
  // ฑ. ุชุงุจุน ูุงฺฉุด ุฑุง ุจุง useCallback ูพุงุฏุงุฑ ูโฺฉูู
  const fetchWarranty = useCallback(async () => {
    if (!id) return;
    try {
      setIsLoading(true);
      setError(null);
      const warrantyData = await getWarrantyById(Number(id));
      setWarranty(warrantyData);
      setFormTitle(warrantyData.title); // ููุฏุงุฑ ุงููู ูุฑู ุฑุง ูู ุชูุธู ูโฺฉูู
    } catch (err) {
      setError(err instanceof Error ? err.message : "ฺฉ ุฎุทุง ูุงุดูุงุฎุชู ุฑุฎ ุฏุงุฏ.");
    } finally {
      setIsLoading(false);
    }
  }, [id]);

  useEffect(() => {
    fetchWarranty();
  }, [fetchWarranty]);

  // --- Handlers ---
  const handleOpenEditModal = () => {
    if (warranty) {
      setFormTitle(warranty.title);
      setIsEditModalOpen(true);
    }
  };

  const handleUpdate = async () => {
    if (!warranty || !formTitle.trim()) return;
    setActionLoading(true);
    try {
      await updateWarranty(warranty.id, { title: formTitle });
      setIsEditModalOpen(false);
      // ฒ. โจ ูพุณ ุงุฒ ุขูพุฏุช ููููุ ุฏุงุฏูโูุง ุฑุง ุฏูุจุงุฑู ูุงฺฉุด ูโฺฉูู โจ
      await fetchWarranty(); 
    } catch (err) {
      alert("ุฎุทุง ุฏุฑ ูุฑุงุด ฺฏุงุฑุงูุช.");
    } finally {
      setActionLoading(false);
    }
  };

  const renderContent = () => {
    if (isLoading) return <p>ุฏุฑ ุญุงู ุจุงุฑฺฏุฐุงุฑ...</p>;
    if (error) return <p>ุฎุทุง: {error}</p>;
    if (!warranty) return <p>ุงูุช ูุดุฏ.</p>;

    return (
      <>
        <div style={{ display: 'flex', flexDirection: 'column', gap: '1.5rem' }}>
          <EntityHeader
            title={warranty.title}
            avatarFallback={warranty.title.charAt(0)}
          >
            <Badge 
              label={warranty.isActive ? "ูุนุงู" : "ุบุฑูุนุงู"}
              variant={warranty.isActive ? "success" : "neutral"}
            />
          </EntityHeader>

          <ActionToolbar onBackClick={() => router.back()}>
            {/* ุฏฺฉูู ูุฑุงุด ุญุงูุง ููุฏุงู ุฑุง ุจุงุฒ ูโฺฉูุฏ */}
            <Button variant="primary" onClick={handleOpenEditModal}>
              ูุฑุงุด ฺฏุงุฑุงูุช
            </Button>
          </ActionToolbar>

          <InfoCard
            title="ุฌุฒุฆุงุช ฺฏุงุฑุงูุช"
            items={[
              { label: "ุดูุงุณู", value: String(warranty.id) },
              { label: "ุนููุงู", value: warranty.title },
              { label: "ูุถุนุช", value: warranty.isActive ? "ูุนุงู" : "ุบุฑูุนุงู" },
              { label: "ุชุงุฑุฎ ุงุฌุงุฏ", value: new Date(warranty.createDate).toLocaleString('fa-IR') },
              { label: "ุขุฎุฑู ุจุฑูุฒุฑุณุงู", value: warranty.updateDate ? new Date(warranty.updateDate).toLocaleString('fa-IR') : "-" },
            ]}
          />
        </div>

        {/* ููุฏุงู ูุฑุงุด */}
        <FormModal
            isOpen={isEditModalOpen}
            onClose={() => setIsEditModalOpen(false)}
            title="ูุฑุงุด ุนููุงู ฺฏุงุฑุงูุช"
            onConfirm={handleUpdate}
            isLoading={actionLoading}
            confirmText="ุฐุฎุฑู ุชุบุฑุงุช"
        >
            <form onSubmit={(e) => { e.preventDefault(); handleUpdate(); }}>
                <Input
                    id="warranty-title-edit"
                    label="ุนููุงู ุฌุฏุฏ"
                    value={formTitle}
                    onChange={(e) => setFormTitle(e.target.value)}
                    required
                    autoFocus
                />
            </form>
        </FormModal>
      </>
    );
  };

  return (
    <YadakchiAdminLayout
      currentPath={pathname}
      renderLink={renderLink}
    >
      {renderContent()}
    </YadakchiAdminLayout>
  );
}